@use "../../partials/variables" as *;
@use "../../partials/animations" as *;
@use "../../partials/mixins" as *;

.hero {
  @include flex-center;
  height: 100vh;
  width: 100%;
  padding: 0 2rem;

  &__container {
    @include flex-center;
    flex-direction: column;
    justify-content: space-evenly;
    height: 18.75rem;
    width: 100%;
    max-width: 37rem;

    @include tablet {
      
    }
  }

  &__search-container {
    display: flex;
    align-items: center;
    border: 1px solid #bfbfbf;
    border-radius: 30px;
    width: 100%;
    height: 3rem;
    min-width: 19.375rem;
    padding-left: 1.25rem;
  }

  &__search-icon {
    width: 1.25rem;
    margin-right: 1rem;
  }

  &__title {
    font-size: 1.25rem;
    font-weight: 400;
    margin-right: .25rem;
    white-space: nowrap;
    overflow: hidden;
    // width: 15.625rem;
    animation: text-reveal .75s linear;

    @include tablet {
      font-size: 1rem;
    }
  }

  &__blinking-cursor {
    width: .75rem;
    height: 1.5rem;
    animation: blink 1s infinite;
  }

  &__btn-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 17rem;
  }

  &__btn {
    @include flex-center;
    text-decoration: none;
    color: $black;
    height: 3rem;
    padding: .75rem 1rem;
    margin-right: 1.25rem;
    background-color: #F8F9FA;
    border: none;
    font-family: Roboto;
    font-size: 0.875rem;
  }
}